// @flow

const gameOverWindow = document.getElementById('gameOverWindow')
const nudeMaja = document.getElementById('nudeMaja')
const dressedMaja = document.getElementById('dressedMaja')

export function showGameOver(youWin: boolean) {
  if (!gameOverWindow) return
  gameOverWindow.innerHTML = ''
  gameOverWindow.appendChild(document.createTextNode(youWin ? 'You win' : 'You loose') )
  gameOverWindow.appendChild(youWin ? nudeMaja : dressedMaja)
  // `
  // <p>${youWin ? 'You win!': 'You loose!'}</p>
  // <img src='' />
  // `
  gameOverWindow.classList.remove('hidden');
}


